<template>
  <!--Question numro : {{index + 1}} Bonnes rponses : {{countRight}}-->
  <div class="row overflow-hidden flex-nowrap">
    <transition name="slide-categorie">
      <div class="col-12 d-flex" :key="progression">
        <div class="row">
          <!-- Les bonnes illustrations s'affichent suivant le bon personnage et la catgorie actuelle -->
          <div
            v-if="character == 'Joris'"
            class="col-sm d-flex align-items-center mb-5 mb-sm-0"
          >
            <img
              class="img-fluid"
              v-if="questionList.questions[index].categorie === 'Prambule'"
              src="@/assets/img/visu-fauche@2x.jpg"
            />
            <img
              class="img-fluid"
              v-else-if="questionList.questions[index].categorie === 'Fauche'"
              src="@/assets/img/visu-fanage@2x.jpg"
            />
            <img
              class="img-fluid"
              v-else-if="questionList.questions[index].categorie === 'Pressage'"
              src="@/assets/img/visu-pressage@2x.jpg"
            />
            <img
              class="img-fluid"
              v-else-if="questionList.questions[index].categorie === 'Stockage'"
              src="@/assets/img/visu-stockage@2x.jpg"
            />
            <img
              class="img-fluid"
              v-else-if="
                questionList.questions[index].categorie === 'Et vous ?'
              "
              src="@/assets/img/visu-et-vous@2x.jpg"
            />
          </div>
          <div
            v-if="character == 'Flora'"
            class="col-sm d-flex align-items-center mb-5 mb-sm-0"
          >
            <img
              class="img-fluid"
              v-if="questionList.questions[index].categorie === 'Prambule'"
              src="@/assets/img/visu-fauche-flora@2x.jpg"
            />
            <img
              class="img-fluid"
              v-else-if="questionList.questions[index].categorie === 'Fauche'"
              src="@/assets/img/visu-fanage-flora@2x.jpg"
            />
            <img
              class="img-fluid"
              v-else-if="questionList.questions[index].categorie === 'Pressage'"
              src="@/assets/img/visu-pressage@2x.jpg"
            />
            <img
              class="img-fluid"
              v-else-if="questionList.questions[index].categorie === 'Stockage'"
              src="@/assets/img/visu-stockage-flora@2x.jpg"
            />
            <img
              class="img-fluid"
              v-else-if="
                questionList.questions[index].categorie === 'Et vous ?'
              "
              src="@/assets/img/visu-et-vous@2x.jpg"
            />
          </div>
          <div
            class="col-sm d-flex align-items-center justify-content-center animation-container"
          >
            <transition name="fade">
              <div v-if="!pushInfo" key="1">
                <question
                  :questionList="questionList.questions"
                  :index="index"
                />
                <!-- On coute l'vnement buttonClicked. Lorsqu'il est mis, on lance la mthode indexChange() -->
                <answers
                  :questionList="questionList.questions"
                  :index="index"
                  @answerPicked="answerCheck($event)"
                />
              </div>
              <div v-else key="2">
                <!--S'il y a un push info, cette div s'affiche et montre le pushInfo ainsi qu'un bouton pour passer  la question suivante-->
                <explanation
                  :answerInfo="answerInfo"
                  :redirect="redirect"
                  @nextQuestion="newQuestion($event)"
                />
              </div>
            </transition>
          </div>
        </div>
      </div>
    </transition>
  </div>
  <div class="row w-100">
    <div class="col-sm">
      <div class="progress-container mt-5 mb-5">
        <div class="progress">
          <div
            class="progress-bar"
            role="progressbar"
            :style="'width: ' + progression + '%'"
            aria-valuenow="10"
            aria-valuemin="0"
            aria-valuemax="100"
          ></div>
        </div>
        <div class="progress-elements">
          <span class="current">Prambule</span>
          <span :class="[progression >= 30 ? 'current' : '']">Fauche</span>
          <span :class="[progression >= 50 ? 'current' : '']">Pressage</span>
          <span :class="[progression >= 70 ? 'current' : '']">Stockage</span>
          <span :class="[progression >= 90 ? 'current' : '']">Et vous ?</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import question from "@/components/Question.vue";
import answers from "@/components/Answers.vue";
import explanation from "@/components/Explanation.vue";
import TC_Wrapper from "vue-tag-commander";

export default {
  name: "Quiz",
  components: {
    /*Chaque import doit tre enregistr dans les components*/
    question,
    answers,
    explanation
  },
  created() {
    if (this.character == "") window.location = "/";

    window.addEventListener("beforeunload", function(e) {
      var confirmationMessage = "";
      e.returnValue = confirmationMessage;
      return confirmationMessage;
    });
  },
  mounted() {
    this.$emit("quiz-loaded");

    const wrapper = TC_Wrapper.getInstance();

    // Mettre  jour les variables souhaites
    wrapper.setTcVars({
      environnement_technique_du_site: window.location.href.includes("www.parlons-fourrage.credit-agricole.fr") ? "prod" : "preprod",
      environnement_nom_du_site: document.title,
      page: "Quiz",
      environnement_distributeur: "",
      environnement_id_CR: "",
      page_url: window.location.href
    });
    document.getElementById('pageVirtuelle').click();
    //console.log('page quiz');
  },
  data() {
    return {
      /* Charge le quiz enregistr dans le store*/
      questionList: this.$store.state.updatedQuiz,
      /*Index est le numro de la question */
      index: 0,
      pushInfo: false,
      redirect: false,
      /* Answerinfo est vide par dfaut, et reoit le push Info si besoin*/
      answerInfo: "",
      progression: 10,
      categorie: "Prambule",
      /* Charge le personnage enregistr dans le store*/
      character: this.$store.state.characterChoice,
      specialScoreTotal: 0
    };
  },
  methods: {
    answerCheck(answer) {
      let questionList = this.questionList.questions;

      // Si la rponse est bonne, on incrmente le compteur de bonnes rponses
      if (answer.right === true) {
        //On enregistre l'incrment dans le store
        //console.log('rightAnswersBefore',this.$store.state.rightAnswers);
        this.$store.commit(
          "countRightAnswers",
          (this.$store.state.rightAnswers += 1)
        );
        //console.log('rightAnswersAfter',this.$store.state.rightAnswers);
      }

      //Pour la question fanage dont les bonnes rponses dpendent de la question prcdente, on a prvu un flag spcial
      if (questionList[this.index].fanageFlag == true) {
        this.dryingFanageQuestion(questionList, answer);
      }

      //Pour les questions de la catgorie "Et vous?", on incrmente un score spcialement ddi  la catgorie.
      if (questionList[this.index].specialScore == true) {
        this.specialScore(answer);
        if (questionList[this.index].finalQuestionFlag === true) {
          this.specialCategory(answer);
        }
      }

      // On ajoute au json la valeur result ainsi que le potentiel conseil de la rponse s'il a choisit faux
      questionList[this.index].result = answer.right;
      questionList[this.index].answerExplained = answer.advice;
      //On met  jour le json dans le store galement
      this.$store.commit("updatingQuiz", questionList);

      //S'il n'y a pas de pushInfo, on passe directement  la question suivante
      if (answer.explanation == undefined) {
        this.index++;
        this.updateCategorie();
      } else {
        // Texte du pushInfo aprs avoir cliqu sur une question
        this.answerInfo = answer.explanation;

        // On affiche le push info aprs avoir rpondu  la question
        this.pushInfo = true;
      }
      // Si on est  la dernire question, on redirige  la dernire page
      if (this.index === questionList.length - 1) {
        this.redirect = true;
      }
    },
    newQuestion() {
      //Lorsqu'il y a un push Info, une fois le bouton "question suivante" clique, on fait disparatre le push info et on passe  la question suivante
      this.pushInfo = false;
      this.index++;
      this.updateCategorie();
    },
    updateCategorie() {
      //Permet de mettre  jour la variable catgorie afin que le tracker puisse suivre
      if (
        this.categorie !== this.questionList.questions[this.index].categorie
      ) {
        this.progression = this.progression + 20;
      }
      this.categorie = this.questionList.questions[this.index].categorie;
    },
    specialScore(answer) {
      this.specialScoreTotal += answer.points;
      //Permet de garder trace des points pour les questions de la catgorie "Et vous? " qui ne sont pas compts dans le score final
    },
    specialCategory(answer) {
      //Permet d'afficher un pushInfo personnalis suivant le score du mini-quiz dans "Et vous ?"
      if (this.specialScoreTotal >= 7) {
        answer.explanation =
          "Bravo ! La vigilance est constante! Vous assurez la qualit de votre foin et les chauffements des fourrages sont minimiss ! Continuez !";
      } else if (this.specialScoreTotal >= 4) {
        answer.explanation =
          "Vous ralisez de temps en temps voire frquemment des points de contrle. Continuez ! Une vigilance accrue permet de limiter les incendies dchauffement tout en assurant la qualit de votre foin.";
      } else {
        answer.explanation =
          "Attention ! Une vigilance accrue tout au long de la fenaison permet une production de foin de meilleure qualit et une diminution des risques dincendies !";
      }
    },
    //Cette fonction permet d'afficher un conseil personnalis suivant le personnage en fonction de la rponse prcdente. Elle se situe en dehors du Json car on ne modifie que les section advice et right.
    dryingFanageQuestion(question, answer) {
      if (this.character === "Joris") {
        if (answer.text === "0 fanage") {
          question[this.index + 1].answers[1].right = false;
          question[this.index + 1].answers[2].right = false;
          question[this.index + 1].answers[0].advice =
            "2-3 jours de schage ne seront srement pas suffisants pour que Joris sche son foin surtout s'il n'y a pas eu de fanage. Joris n'aura srement pas un foin sec avant le pressage.";
          question[this.index + 1].answers[1].advice =
            "4-5 jours de schage auraient pu tre suffisants pour que Joris sche son foin mais il n'a pas effectu de fanages et les conditions mtorologiques ne sont pas des plus adquates. En laissant scher son foin plus longtemps, Joris diminue les risques d'chauffements.";
          question[this.index + 1].answers[2].advice =
            "6 jours de schage pourraient tre suffisants Mais pour tre sr d'avoir un foin sec au pressage, Joris aurait d le faner.";
        } else if (answer.text === "Entre 1 et 3 fanages") {
          question[this.index + 1].answers[0].advice =
            "Mme avec des fanages, 2-3 jours de schage ne seront srement pas suffisants pour faire scher son foin, surtout si l'hygromtrie de l'air est leve. Joris prend le risque de presser un foin humide ! Son foin va srement s'chauffer";
        } else if (answer.text === "Plus de 4 fanages") {
          question[this.index + 1].answers[0].advice =
            "2-3 jours de schage pourraient tre suffisant avec un grand nombre de fanages. Mais Joris doit rester vigilant, car les conditions mtorologiques ne sont pas des plus adquates pour le schage. S'il veut viter au maximum les chauffements, il doit laisser scher son foin plus longtemps.";
        }
      } else if (this.character === "Flora") {
        if (answer.text === "0 fanage") {
          question[this.index + 1].answers[1].right = false;
          question[this.index + 1].answers[2].right = false;
          question[this.index + 1].answers[0].advice =
            "2-3 jours de schage ne seront srement pas suffisants pour que Flora sche son foin surtout s'il n'y a pas eu de fanage. Flora n'aura srement pas un foin sec avant le pressage.";
          question[this.index + 1].answers[1].advice =
            "4-5 jours de schage auraient pu tre suffisants pour que Flora sche son foin mais elle n'a pas effectu de fanages et les conditions mtorologiques ne sont pas des plus adquates. En laissant scher son foin plus longtemps, Flora diminue les risques d'chauffements.";
          question[this.index + 1].answers[2].advice =
            "6 jours de schage pourraient tre suffisants Mais pour tre sr d'avoir un foin sec au pressage, Flora aurait d le faner.";
        } else if (answer.text === "Entre 1 et 3 fanages") {
          question[this.index + 1].answers[0].advice =
            "Mme avec des fanages, 2-3 jours de schage ne seront srement pas suffisants pour faire scher son foin, surtout si l'hygromtrie de l'air est leve. Flora prend le risque de presser un foin humide ! Son foin va srement s'chauffer";
        } else if (answer.text === "Plus de 4 fanages") {
          question[this.index + 1].answers[0].advice =
            "2-3 jours de schage pourraient tre suffisant avec un grand nombre de fanages. Mais Flora doit rester vigilant, car les conditions mtorologiques ne sont pas des plus adquates pour le schage. Si elle veut viter au maximum les chauffements, elle doit laisser scher son foin plus longtemps.";
        }
      }
    }
  }
};
</script>
